import React from "react";
import PropTypes from "prop-types";

import st from "./style";
import IconPlaceHolderUser from '../../icons/IconPlaceHolderUser';

class UserAvatar extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      
    };
  }

  render() {
    return (
      <div style={{...st.container(this.props),...this.props.style}}>
          
        {this.props.userImage?
          <img style={st.image} src={this.props.userImage}/>
          :<IconPlaceHolderUser style={st.image}/>
        }
        {
          this.props.notifications>0?
          <div style={st.ctnNotifications}> {this.props.notifications} </div>
          :null
        }
        
      </div>
    );
  }
}

UserAvatar.propTypes = {
  onClick: PropTypes.func,
  userImage: PropTypes.string,
  size: PropTypes.number,
  notifications: PropTypes.number,
  style: PropTypes.object,
};
UserAvatar.defaultProps = {
  onClick: () => {},
  size: 50,
  notifications:null,
  style: {},
};

export default UserAvatar;
